<template>
  <HomeFrame :config="theFrameConfig" @action="handleAction">
    <MyApps :value="theItems" />
  </HomeFrame>
</template>
  
<script>
import MyApps from "./executables.vue";

export default {
  name: "home-executable-index",
  components: { MyApps },

  computed: {
    theFrameConfig() {
      return {
        pager: false,
        home: true,
        nav: true,
        search: true,
        searchCategory: "应用",
      };
    },

    theItems() {
      return this.$store.getters["executable/afterFilter"];
    },
  },

  data() {
    return {};
  },

  methods: {
    handleAction(p) {
      let name = p.name;
      if (name == "refresh") {
        this.fetch();
      } else if (name == "add") {
        this.add();
      } else if (name == "applyFilter") {
        this.applyFilter(p.value);
      } else {
        console.warn("unsupported action: " + name);
      }
    },

    applyFilter(value) {
      let filter = value;
      this.$store.commit("executable/update", { filter });
    },

    fetch() {
      this.$store.dispatch("executable/fetch");
    },

    add() {
      this.$router.push("/import-executable");
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  