<template>
  <div>
    <div>
      <h2>{{ value.name }}</h2>
    </div>

    <div class="description">{{ value.description }}</div>

    <el-divider />

    <div class="path">{{ value.path }}</div>

    <!-- <el-divider /> -->

    <el-button type="success" @click="handleRun"> 运行 </el-button>
  </div>
</template>

<script>
export default {
  name: "home-exe-detail",

  methods: {
    handleRun() {
      let executable = this.value;
      let intent = { executable };
      let vo = {
        intents: [intent],
      };
      this.$store.dispatch("intent/run", vo);
    },
  },

  props: { value: Object },
};
</script>
